	module.exports = {"data":{"markdownRemark":{"html":"<h2>Human Computer Interaction</h2>\n<p>Human-Computer Interaction (HCI) is a field of study that focuses on the design of computer technology and, in particular, the interaction between humans\n(the users) and computers. It encompasses multiple disciplines, such as computer science, cognitive science, and human-factors engineering. While it originally\nconcerned computers, HCI has since expanded to cover almost all forms of information technology design.</p>\n<p>Humans interact with computers mostly through graphical user interfaces (GUI), and sometimes through voice user interfaces (VUI).  In the field of User\nExperience/User Interaction (UX/UI), human-computer interaction is studied to ensure optimal user satisfaction and engagement with a piece of software.</p>\n<h3>Design Principles</h3>\n<p>Several experimental design principles can be applied to designing or evaluating a user interace:</p>\n<ul>\n<li>Early focus on user and task: Establish how many users are needed to perform the task(s) and determine who the appropriate users should be; someone who has never used the interface, and will not use the interface in the future, is most likely not a valid user. In addition, define the task(s) the users will be performing and how often the task(s) need to be performed.</li>\n<li>Empirical measurement: Test the interface early on with real users who come in contact with the interface on a daily basis. Keep in mind that results may vary with the performance level of the user and may not be an accurate depiction of the typical human-computer interaction. Establish quantitative usability specifics such as: the number of users performing the task(s), the time to complete the task(s), and the number of errors made during the task(s).</li>\n<li>\n<p>Iterative design: After determining the users, tasks, and empirical measurements to include, perform the following iterative design steps:</p>\n<ol>\n<li>Design the user interface</li>\n<li>Test</li>\n<li>Analyze</li>\n<li>Repeat</li>\n</ol>\n</li>\n</ul>\n<h3>Relation to User Experience Design (UX)</h3>\n<p>In many ways, HCI was the forerunner that would grow to become what we now call “User Experience (UX) Design.” Despite that, some differences persist between HCI and UX design. Practitioners of HCI tend to be more academically focused, and are involved in scientific research and developing empirical understandings of users. UX designers, on the other hand, tend to be industry-focused, and most UX designers are involved in building a product or service—for example, a smartphone app or a website. Regardless of this difference, the practical considerations for products that UX designers concern themselves with have direct links to the findings of HCI specialists about the mindsets of users. Due to this, there is little point in separating these realms to any great extent.</p>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Human%E2%80%93computer_interaction\">Human Computer Interaction (Wikipedia)</a></li>\n<li><a href=\"https://www.interaction-design.org/literature/book/the-encyclopedia-of-human-computer-interaction-2nd-ed/human-computer-interaction-brief-intro\">The Encyclopedia of Human-Computer Interaction, 2nd Ed.</a></li>\n</ul>","fields":{"slug":"/user-experience-design/human-computer-interaction/"},"frontmatter":{"title":"Human Computer Interaction"}}},"pathContext":{"slug":"/user-experience-design/human-computer-interaction/"}}

